export default {
  props: {
    // 
    urls: {
      type: Array,
      default: () => ([]),
    },
    // 
    maxCount: {
      type: [String, Number],
      default: 5,
    },
    round: {
      type: Boolean,
      default: true,
    },
    // 
    mode: {
      type: String,
      default: 'scaleToFill',
    },
    // maxCount
    showMore: {
      type: Boolean,
      default: true,
    },
    // 
    size: {
      type: [String, Number],
      default: 40,
    },
    // 
    keyName: {
      type: String,
      default: '',
    },
    // 
    gap: {
      type: [String, Number],
      validator(value) {
        return value >= 0 && value <= 1;
      },
      default: '0.5',
    },
    // 
    extraValue: {
      type: [Number, String],
      default: '',
    },
    customStyle: {
      type: String,
      default: '',
    },
  },
};
